<template>
  <div>
    <b-breadcrumb :items="menus"/>
    <b-container fluid>
      <b-table bordered responsive hover :items="reportList" :fields="fields" head-variant='light' show-empty :empty-text="tableEmptyText">
        <template slot="show_details" slot-scope="row">
          <b-link @click="row.item._showDetails=!row.item._showDetails">{{row.item._showDetails?'收起':'展开'}}</b-link>
        </template>
        <!-- 详情模板 -->
        <template slot="row-details" slot-scope="row">
          <b-card>
            <b-row class="mb-2">
              <!-- <b-col sm="3">异常类型：</b-col>
              <b-col sm="3">{{ row.item.abnormal }}</b-col> -->
              <b-col sm="2">报告生成日期：</b-col>
              <b-col sm="4">{{ row.item.report_date }}</b-col>
              <b-col sm="2">报告审阅日期：</b-col>
              <b-col sm="4">{{ row.item.report_review_date }}</b-col>
              <b-col sm="2">报告发送日期：</b-col>
              <b-col sm="9">{{ row.item.report_deliver_date }}</b-col>
              <b-col sm="2">报告存储位置：</b-col>
              <b-col sm="9">{{ row.item.report_loc }}</b-col>
            </b-row>
          </b-card>
        </template>
        <template slot="vip" slot-scope="row">
          <span v-show="row.item.vip==='1'" style="color:red">VIP</span>
        </template>
        <template slot="report_risk" slot-scope="row">
          <!-- <span :style="(row.item.report_risk/1)<10?'color:red':'color:#41babc'">{{report_risk}}</span> -->
          <span>{{row.item.report_risk}}</span>
        </template>
        <template slot="operate" slot-scope="row">
          <b-button size="sm" class="mr-2" variant="info" @click="show(row)">
           查看
          </b-button>
          <b-button size="sm" class="mr-2" variant="success" @click="send(row)" :disabled="sendFlag" v-if="(row.item.from==='wechat')&&(row.item.report_status==='未发送'||row.item.report_status==='发送失败')">
            {{row.item.sendTitle}}
          </b-button>
        </template>
      </b-table>
      <div class="paginationCon">
        <b-pagination size="md" :number-of-pages="10" :total-rows="total" v-model="currentPage" @input="changePage" :per-page="10"></b-pagination>
      </div>
    </b-container>
  </div>
</template>
<script>

  export default{
    components:{

    },
    mounted(){
      
      this.getTableData()
      
    },
    data () {
      return {
        menus: [{
          text: '报告管理',
          href: '#'
        }],
        fields: {
          show_details:{
            label:'详情'
          },
          order_id:{
            label:'申请单号'
          },
          name:{
            label:'受检者姓名'
          },
          product:{
            label:'产品名'
          },
          vip:{
            label:'VIP'
          },
          report_risk:{
            label:'风险级别'
          },
          report_reviewer:{
            label:'报告审阅人'
          },
          report_review_status:{
            label:'报告审阅状态'
          },
          // 分析中， 未发送 ，已发送  默认分析中
          report_status:{
            label:'报告状态'
          },
          operate:{
            label:'操作'
          }
        },
        tableEmptyText:'正在请求数据...',
        sendFlag:false,
        reportList: [],
        currentPage:1,
        total:0
      }
    },
    methods:{
      getTableData(){
        this.$Loading.start()
        this.tableEmptyText = "正在请求数据..."
        this.$http.post("/api/Reportpdf/list",qs.stringify({page:this.currentPage,qc_review_status:'合格',report_review_status:"合格",report_review_status2:'合格'})).then(response=>{
          // console.log(response.data)
          this.reportList=response.data.data
          this.reportList.forEach(item=>{
            this.$set(item,'_showDetails',false)
            this.$set(item,'sendTitle','发送到微信')
          })
          Number(response.data.total)===0?this.tableEmptyText = "[空]":null
          this.total = Number(response.data.total)
          this.$Loading.finish()
        })
      },
      show(row){
        let address = row.item.report_loc.slice(20,row.item.report_loc.length)
        window.open("/storage/report_pdf/"+address)
      },
      send(row){
        this.sendFlag = true
        row.item.sendTitle = '发送中...'
        this.$Loading.start()
        this.$http.post("/api/ace/send",qs.stringify({order_id:row.item.order_id})).then(response=>{
          if(response.data.success){
            this.sendFlag = false
            // this.$Message.success({content:'发送成功！'})
            this.$Loading.finish()
            this.getTableData()
          }else{
            this.sendFlag = false
            // this.$Message.error({content:'发送失败！'})
            row.item.sendTitle = '发送到微信'
            this.$Loading.error()
          }
        }).catch(err=>{
          this.sendFlag = false
          // this.$Message.error({content:'发送失败！'})
          row.item.sendTitle = '发送到微信'
          this.$Loading.error()
        })

      },
      changePage(){
        this.getTableData()
      }
    }

  }
</script>
<style lang='less' scoped>
@import '../../../less/init.less';
#plasm_use_unit{
  border-bottom-left-radius: 0;
  border-top-left-radius: 0;
}
#plasm_unit{
  border-bottom-left-radius: 0;
  border-top-left-radius: 0;
}
</style>


